.activities-ss2 {
    position: relative;
    margin-bottom: 50px;
    .list-items {}
    .item {
        margin-bottom: 30px;
        padding: 0px 30px;
        .img {
            margin-bottom: 15px;
            width: 100%;
            height: 351.75px;
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        h3 {
            color: map-get($mau, main-2);
            font-size: 30px;
            font-weight: 700;
            font-family: "lora", sans-serif;
            margin-bottom: 20px;
        }
        p {
            color: #000;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            font-size: 16px;
            line-height: 1.42857143;
            text-align: justify;
            margin: 0 0 20px;
        }
        @media (max-width: 1024.98px) {
            padding: 0 15px;
        }
        @media (max-width: 767.98px) {
            padding: 0 10px;
            flex: 0 0 100%;
        }
    }
}

.img-bottom-right {
    position: absolute;
    bottom: 30px;
    right: 0;
    width: 600px;
    @media (max-width: 1439.98px) {
        img {
            float: right;
            margin-left: auto;
            width: 85%;
        }
    }
    @media (max-width: 1024.98px) {
        img {
            float: right;
            margin-left: auto;
            width: 60%;
        }
    }
    @media (max-width: 767.98px) {
        display: none;
    }
}