.index-3 {
	padding: 100px 0;

	.list-item-index-3 {
		.item {
			margin-bottom: 30px;
		}
	}

	@media (max-width: 1024.98px) {
		padding: 50px 0;
	}

	@media (max-width: 575.98px) {
		padding: 25px 0;

		.list-item-index-3 {
			.item {
				&:last-child {
					margin-bottom: 0;
				}
			}
		}
	}

	.list-item-index-3 {
		.item {
			margin-bottom: 15px;
		}
	}
}