section.bac-home {
    position: relative;
    display: block;
    .inner-bac {
        padding-top: 100px;
        h2.title-bac {
            position: relative;
            font-size: 40px;
            margin-bottom: 20px;
        }
        .content-lv-1 {
            h3 {
                margin-bottom: 20px;
                font-size: 30px;
                color: #0A1F44;
            }
        }
        .content-lv-2 {
            padding-left: 20px;
            p {
                font-size: 16px;
                margin-bottom: 20px;
                a {
                    color: #AD132A;
                }
            }
            h5 {
                color: #AD132A;
                font-size: 18px;
                margin-bottom: 20px;
            }
        }
        .content-lv-3 {
            padding-left: 40px;
            p {
                padding-bottom: 20px;
                text-align: justify;
            }
            a {
                display: inline-block;
                text-decoration: underline;
                font-size: 16px;
                color: #AD132A;
                padding-bottom: 20px;
            }
        }
        .tutor-login {
            padding: 50px 0;
            .video-tutor-login {
                justify-content: center;
                .box-video {
                    position: relative;
                    width: 300px;
                    height: 300px;
                    margin: 0 10px;
                }
            }
        }
    }
}

@media(max-width:979px) {
    .inner-bac {
        padding-top: 20px !important;
        h2.title-bac {
            font-size: 28px !important;
        }
    }
    .box-video {
        width: 100% !important;
        margin-bottom: 10px !important;
    }
}

@media(max-width:575px) {
    .inner-bac {
        h2.title-bac {
            font-size: 28px !important;
        }
        .content-lv-1 {
            h3 {
                font-size: 18px;
                margin-bottom: 10px !important;
            }
        }
        .content-lv-2 {
            p {
                margin-bottom: 10px !important;
            }
            h5 {
                font-size: 16px !important;
                margin-bottom: 10px !important;
            }
        }
        .content-lv-3 {
            p {
                font-size: 16px !important;
                padding-bottom: 10px !important;
            }
        }
    }
}